* {
  box-sizing: content-box;
  transition: all $tr-fast;
}

body {
  background-color: $lightest;
  color: $darkest;
  font-family: $font-sans-serif;
  font-size: $m;
  font-weight: $fw-light;
}

a {
  color: $darkest;
  text-decoration: none;
}

/* Title & Subtitle
---------------------------------------------------------------------------- */

.title,
.subtitle,
.chapter {
  position: relative;
  margin-bottom: $l;
  font-size: $l;
  line-height: $lh-headlines;
}

.subtitle,
.chapter {
  margin-top: 0;
  font-weight: $fw-regular;
}

.title {
  font-size: $xl;
  font-weight: $fw-semibold;
}

.inner-title {
  margin-top: $xxl;
  margin-bottom: $half;
  font-weight: $fw-regular;
}

.title + .subtitle{
  margin-top: -$m;
}

/* Text
---------------------------------------------------------------------------- */

p,
li,
th,
td,
dt,
dd {
  hyphens: auto;
  line-height: $lh-normal;
}

/* Headline
---------------------------------------------------------------------------- */

h2 {
  margin-top: 0;
  font-size: $l;
  font-weight: $fw-regular;
  margin-bottom: $xxxl;
  color: $accent-dark;
}

h3 {
  font-size: $m;
  font-weight: $fw-bold;
  margin-top: $xl;
}

